.screens {
  max-height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.screen {
  height: 100vh;
  display: flex;
  align-items: flex-end;
  scroll-snap-align: start;
  padding-bottom: 120px;
}
.screen-inner {
  flex: 0 0 100%;
  max-width: 100%;
}

.main-screen {
  height: 100svh;
  padding-bottom: 140px;
  background: #21863b;
  position: relative;
  z-index: 1;
}
.main-screen .inner {
  position: relative;
}
.main-screen-content {
  max-width: 950px;
  padding-right: 150px;
}
.main-screen-logo {
  width: 500px;
  max-width: 30vw;
  margin-bottom: 60px;
}
.main-screen-title {
  font-size: 80px;
  margin-bottom: 18px;
  color: #FFF;
  line-height: 1;
  font-family: "Lack";
}
.main-screen-txt {
  color: #B3B5B6;
}
.main-screen-3d {
  position: absolute;
  right: 50px;
  bottom: 0;
  width: 132px;
  display: block;
  transition: transform 0.3s;
}
.main-screen-3d svg {
  fill: none;
}
.main-screen-3d:hover {
  transform: rotate(-180deg);
}
.main-screen-video {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
}
@media (max-width: 1440px) {
  .main-screen-logo {
    max-width: 35vw;
    margin-bottom: 35px;
  }
  .main-screen-title {
    font-size: 68px;
  }
  .main-screen-3d {
    right: 40px;
  }
}
@media (max-width: 1024px) {
  .main-screen {
    align-items: flex-start;
    padding-top: 117px;
  }
  .main-screen-content {
    padding-right: 0;
  }
  .main-screen-logo {
    width: 320px;
    max-width: 70%;
  }
  .main-screen-title {
    font-size: 8vw;
  }
  .main-screen-3d {
    display: none;
    visibility: hidden;
  }
}
@media (max-width: 599px) {
  .main-screen-title {
    font-size: 9vw;
  }
  .main-screen-txt {
    font-size: 16px;
    font-size: 1rem;
  }
  .main-screen-txt br {
    display: none;
  }
}
@media (max-width: 480px) {
  .main-screen-title {
    font-size: 9.6vw;
    margin-bottom: 0;
  }
}